<template>
  <a-layout class="back" style="height: 100vh">
    <siderLeft></siderLeft>
    <siderRight></siderRight>
  </a-layout>
</template>

<script>
import siderLeft from './siderLeft'
import siderRight from './siderRight'
// import {checkLogin, checkShopId} from '../../common/js/checkLogin'
import {getUrlKey} from '../../common/js/common'
export default {
  name: 'back',
  components: {
    siderLeft,
    siderRight
  },
  data () {
    return {
      appId: ''
    }
  },
  created () {
    this.checkMyLogin()
  },
  mounted () {
  },
  methods: {
    checkMyLogin () {
      if (getUrlKey('auth_code')) {
        // 新增
        let values = {}
        // values.appId = 'wxf5dcb478590947a0'
        // values.appId = getUrlKey('appId')
        values.authCode = getUrlKey('auth_code')
        // values.storeId = getUrlKey('storeId')
        this.addWechatAuth(values)
        history.go(-2)
        // this.goToPage()
      }
    },
    async addWechatAuth (value) {
      let res = await this.$Http.wechatAppAuthAdd(value)
      if (res.code === 0) {
        console.log('授权成功')
      }
    },
    goToPage () {
      console.log('跳转')
      this.bus.$emit('goToPage', 'wechatAppAuth')
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>



